.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.innerTopChild {
  margin: auto;
}

.topchild {
  text-align: center;
  display: flex;
  width:100%;
  padding: 1px;
  z-index:100;
  border: #1111c0 1px solid;
  background-color: #22c0c0;
}

.Top {
  background-color: #c0c0c0;
  display: flex;
  top:0;
  width:100%;
  height: 70px;
  z-index:100;
}

.Mid {
  background-color: rgb(232, 187, 187);
  width: 100%;
  height: 100vh;
}

.Bot {
  background-color: rgb(174, 120, 39);
  width: 100%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
